<template>
  <div class="container">
    <div class="wrapper">
      <b-col cols="10">
        <h4>Select property to create or edit its facts sheet</h4>
      </b-col>
      <div class="GFS-Center-Table">
        <table class="table table-hover">
          <thead class="GFS-TNave">
            <tr>
              <th scope="col">#</th>
              <th scope="col">Country</th>
              <th scope="col">State</th>
              <th scope="col">City</th>
              <th scope="col">Hotel</th>
              <th scope="col"></th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(property, index) in properties" :key="index">
              <th scope="row">{{ index + 1 }}</th>
              <td>{{ property.country }}</td>
              <td>{{ property.district }}</td>
              <td>{{ property.city }}</td>
              <td>{{ property.hotel_name }}</td>
              <button>
              <router-link :to="`/property/${property.id}/`">Edit {{ property.hotel_name }}
              </router-link>
              </button>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
    };
  },
  async created() {
    await this.getProperties();
  },
  computed: {
    properties() {
      return this.$store.getters['property/getProperties'];
    },
    isAuthenticated() {
      return this.$store.getters['user/isAuthenticated'];
    },
  },
  methods: {
    async getProperties() {
      await this.$store
        .dispatch('property/getInfo')
        .then((response) => {
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    showEdit(property) {
      this.propertyToEdit = {
        id: property.id,
        country: property.country,
        district: property.district,
        city: property.city,
        hotel_name: property.hotel_name,
      };
      this.showModal = true;
    },
    async save() {
      if (this.propertyToEdit.id === 0) {
        await this.AddProperty();
      } else {
        await this.updateProperty();
      }
      this.showModal = false;
    },
    cancel() {
      this.showModal = false;
    },
  },
};
</script>
