<template>
  <div class="container">
    <div class="wrapper">
      <b-row>
        <b-col cols="10">
          <h4>Authorized sub-users to update facts </h4>
        </b-col>
      </b-row>
      <b-row>
    <b-col cols="7">
      <div class="GFS-Center-Table">
        <table class="table table-hover">
          <thead class="GFS-TNave">
            <tr>
              <th scope="col">#</th>
              <th scope="col">Username</th>
              <th scope="col">Title</th>
              <th scope="col">E-mail</th>
              <th scope="col">Hotels assigned</th>
              <th scope="col"></th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(property, index) in properties" :key="index">
              <th scope="row">{{ index + 1 }}</th>
              <td>{{ property.country }}</td>
              <td>{{ property.district }}</td>
              <td>{{ property.city }}</td>
              <td>{{ property.hotel_name }}</td>
              <button @click="showEdit(property)">Edit</button>
              <button @click="removeProperty(property)">Remove</button>
            </tr>
          </tbody>
        </table>
      </div>
      </b-col>
      <b-col>
          <h5>add users</h5>
        <b-col cols="4">
          <button class="button" @click="showAdd()">Add new user</button>
        </b-col>
        <transition name="fade" appear>
          <div
            class="modal-overlay"
            v-if="showModal"
            @click="showModal = false"
          ></div>
        </transition>
      </b-col>
      </b-row>
      <transition name="slide" appear>
        <div
          class="modal"
          v-if="showModal"
          style="align: left; display: block; width: 100%; float: left">
          <form v-on:submit.prevent @submit="save()">
              <b-form-group
                label="Select Country"
                label-for="id_Country_Name"
                label-cols-sm="3"
                label-align-sm="right">
              <select
                class="form-control"
                id="id_Country_Name"
                required
                v-model="propertyToEdit.country">
                <option value="null" selected>--Select Country--</option>
                <option
                  v-for="(k, index) in countries"
                  :key="index"
                  :value="index">
                  {{ k }}
                </option>
              </select>
              </b-form-group>
              <b-form-group
                label="State"
                label-for="id_State_Name"
                label-cols-sm="3"
                label-align-sm="right">
                <input
                  class="form-control"
                  type="text"
                  id="id_State_Name"
                  v-model="propertyToEdit.district"
                  required>
              </b-form-group>
              <b-form-group
                label="City"
                label-for="id_City_Name"
                label-cols-sm="3"
                label-align-sm="right">
                <input
                  class="form-control"
                  type="text"
                  id="id_City_Name"
                  v-model="propertyToEdit.city"
                  required>
              </b-form-group>
              <b-form-group
                label="Hotel Name"
                label-for="id_Hotel_Name"
                label-cols-sm="3"
                label-align-sm="right">
                <input
                  class="form-control"
                  type="text"
                  id="id_Hotel_Name"
                  v-model="propertyToEdit.hotel_name"
                  required>
              </b-form-group>
            <input type="submit" class="modalbutton" value="Save">
            <button type="button" class="modalbutton" @click="cancel()">Cancel</button>
          </form>
        </div>
      </transition>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      countries: this.$store.getters['countries/getCountries'],
      showModal: false,
      properties: [],
      propertyToEdit: null,
    };
  },
  async created() {
    await this.getProperties();
  },
  methods: {
    async getProperties() {
      this.$store.dispatch('view/load', true);
      await this.$store
        .dispatch('property/getInfo')
        .then((response) => {
          console.log(response);
          this.properties = this.$store.getters['property/getProperties'];
          this.$store.dispatch('view/load', false);
        })
        .catch((error) => {
          console.log(error);
          this.$store.dispatch('view/load', false);
        });
    },
    showEdit(property) {
      this.propertyToEdit = {
        id: property.id,
        country: property.country,
        district: property.district,
        city: property.city,
        hotel_name: property.hotel_name,
      };
      this.showModal = true;
    },
    showAdd() {
      this.propertyToEdit = {
        id: 0,
        country: null,
        city: null,
        district: null,
        hotel_name: null,
      };
      this.showModal = true;
    },
    async updateProperty() {
      await this.$store
        .dispatch('property/putInfo', this.propertyToEdit)
        .then((response) => {
          console.log(response);
          this.properties = this.$store.getters['property/getProperties'];
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async AddProperty() {
      await this.$store
        .dispatch('property/postInfo', this.propertyToEdit)
        .then((response) => {
          console.log(response);
          this.properties = this.$store.getters['property/getProperties'];
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async removeProperty(propertyToRemove) {
      await this.$store
        .dispatch('property/deleteInfo', propertyToRemove)
        .then((response) => {
          console.log(response);
          this.properties = this.$store.getters['property/getProperties'];
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async save() {
      if (this.propertyToEdit.id === 0) {
        await this.AddProperty();
      } else {
        await this.updateProperty();
      }
      this.showModal = false;
    },
    cancel() {
      this.showModal = false;
    },
  },
};
</script>
<style>
.modal-overlay {
  position: center;
  right: 0;
  bottom: 0;
  z-index: 98;
  background-color: rgba(0, 0, 0, 0.3);
}

.modal {
  position: fixed;
  top: 50% !important;
  left: 50% !important;
  transform: translate(-50%, -50%);
  z-index: 99;
  width: 100%;
  max-width: 400px;
  background-color: #fff;
  border-radius: 16px;
  padding: 25px;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.slide-enter-active,
.slide-leave-active {
  transition: transform 0.5s;
}

.slide-enter,
.slide-leave-to {
  transform: translateY(-50%) translateX(100vw);
}
</style>
