<script>
export default {
  name: 'RoomInstance',
  props: {
    roomId: { type: Number },
    isSelected: { type: Boolean, default: false },
  },
  data() {
    return {
      roomName: '',
      onGroundFloor: false,
    };
  },
  computed: {
    tabName() {
      return this.roomName === '' || this.roomName === null ? 'New Room' : this.roomName;
    },
  },
  methods: {
    activate() {
      this.$emit('activated', {
        roomId: this.roomId,
      });
    },
    linkClass() {
      return this.isSelected ? ['bg-primary', 'text-light'] : ['bg-light', 'text-info'];
    },
  },
};
</script>
