import axios from 'axios';

export default ({
  namespaced: true,
  state: {
    property: {
      id: 0,
      boardsavailable: [],
      HotelBreakfastTypes: [],
      HotelLunchTypes: [],
      HotelDinnerTypes: [],
      HotelFoodSpecials: [],
      DinnerDressCodeRequired: false,
      HotelHasChristmasDinner: false,
      HotelChristmasDinnerCharges: null,
      HotelChristmasDinnerObligatory: false,
      HotelChristmasDinnerDrinks: false,
      HotelHasNYEDinner: false,
      HotelNYECharges: null,
      HotelNYEObligatory: false,
      HotelNYEDrinks: false,
      HotelHasOtherGala: false,
      HotelGalaName: null,
      HotelGalaDate: null,
      HotelGalaCharges: null,
      HotelGalaObligatory: false,
      HotelGalaDrinks: false,
    },
  },
  mutations: {
    updateProperty(state, newInfo) {
      state.property = newInfo;
    },
  },
  actions: {
    postInfo(context, newInfo) {
      axios.defaults.headers.common.Authorization = `Token ${context.rootState.user.token}`;
      console.log(`Token is ${context.rootState.user.token}`);
      console.log(newInfo);
      return new Promise((resolve, reject) => {
        axios({
          method: 'post',
          url: 'http://127.0.0.1:8000/api/info/create/',
          data: newInfo,
        }).then((response) => {
          const savedInfo = {
            id: response.data.id,
            boardsavailable: response.data.boardsavailable,
            HotelBreakfastTypes: response.data.HotelBreakfastTypes,
            HotelLunchTypes: response.data.HotelLunchTypes,
            HotelDinnerTypes: response.data.HotelDinnerTypes,
            HotelFoodSpecials: response.data.HotelFoodSpecials,
            DinnerDressCodeRequired: response.data.DinnerDressCodeRequired,
            HotelHasChristmasDinner: response.data.HotelHasChristmasDinner,
            HotelChristmasDinnerCharges: response.data.HotelChristmasDinnerCharges,
            HotelChristmasDinnerObligatory: response.data.HotelChristmasDinnerObligatory,
            HotelChristmasDinnerDrinks: response.data.HotelChristmasDinnerDrinks,
            HotelHasNYEDinner: response.data.HotelHasNYEDinner,
            HotelNYECharges: response.data.HotelNYECharges,
            HotelNYEObligatory: response.data.HotelNYEObligatory,
            HotelNYEDrinks: response.data.HotelNYEDrinks,
            HotelHasOtherGala: response.data.HotelHasOtherGala,
            HotelGalaName: response.data.HotelGalaName,
            HotelGalaDate: response.data.HotelGalaDate,
            HotelGalaCharges: response.data.HotelGalaCharges,
            HotelGalaObligatory: response.data.HotelGalaObligatory,
            HotelGalaDrinks: response.data.HotelGalaDrinks,
          };
          context.commit('updateProperty', savedInfo);
          resolve(savedInfo);
        }).catch((error) => {
          reject(error);
        });
      });
    },
    getInfo(context, infoId) {
      axios.defaults.headers.common.Authorization = `Token ${context.rootState.user.token}`;
      return new Promise((resolve, reject) => {
        axios({
          method: 'get',
          url: `http://127.0.0.1:8000/api/info/${infoId}/`,
        }).then((response) => {
          const retrievedInfo = {
            id: response.data.id,
            boardsavailable: response.data.boardsavailable,
            HotelBreakfastTypes: response.data.HotelBreakfastTypes,
            HotelLunchTypes: response.data.HotelLunchTypes,
            HotelDinnerTypes: response.data.HotelDinnerTypes,
            HotelFoodSpecials: response.data.HotelFoodSpecials,
            DinnerDressCodeRequired: response.data.DinnerDressCodeRequired,
            HotelHasChristmasDinner: response.data.HotelHasChristmasDinner,
            HotelChristmasDinnerCharges: response.data.HotelChristmasDinnerCharges,
            HotelChristmasDinnerObligatory: response.data.HotelChristmasDinnerObligatory,
            HotelChristmasDinnerDrinks: response.data.HotelChristmasDinnerDrinks,
            HotelHasNYEDinner: response.data.HotelHasNYEDinner,
            HotelNYECharges: response.data.HotelNYECharges,
            HotelNYEObligatory: response.data.HotelNYEObligatory,
            HotelNYEDrinks: response.data.HotelNYEDrinks,
            HotelHasOtherGala: response.data.HotelHasOtherGala,
            HotelGalaName: response.data.HotelGalaName,
            HotelGalaDate: response.data.HotelGalaDate,
            HotelGalaCharges: response.data.HotelGalaCharges,
            HotelGalaObligatory: response.data.HotelGalaObligatory,
            HotelGalaDrinks: response.data.HotelGalaDrinks,
          };
          context.commit('updateProperty', retrievedInfo);
          resolve(retrievedInfo);
        }).catch((error) => {
          reject(error);
        });
      });
    },
  },
  getters: {
    getProperty(state) {
      return state.property;
    },
  },
});
