import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import DashboardHome from '../components/DashboardHome.vue';
import AddProperty from '../components/AddProperty.vue';
import PropertyFacts from '../components/PropertyFacts.vue';
import AddFacts from '../components/AddFacts.vue';
import AddPhotos from '../components/AddPhotos.vue';
import Authorizations from '../components/AuthorizedUsers.vue';
import Partners from '../components/PartnersContracts.vue';
import MainInfo from '../components/facts/MainInfo.vue';
import EnviromentFactsComp from '../components/facts/Enviroment.vue';
import WellnessFactsComp from '../components/facts/SportsWellness.vue';
import ChildrenFactsComp from '../components/facts/Children.vue';
import FacilitiesFactsComp from '../components/facts/Facilities.vue';
import RoomsFactsComp from '../components/facts/Rooms.vue';

import LoginForm from '../login/LoginPage.vue';
import SignupForm from '../login/SignupPage.vue';
import ForgotPassword from '../login/ForgotPassword.vue';
import ActivateUser from '../login/ActivateSuccess.vue';
import ResetPassword from '../login/ResetSuccess.vue';
import ResendActivation from '../login/ResendActivation.vue';
import store from '../store';
/* added from different project dashboard */
import Table from '../components/Table.vue';
import Card from '../components/Cards/Card.vue';
import ChartCard from '../components/Cards/ChartCard.vue';
import StatsCard from '../components/Cards/StatsCard.vue';

Vue.use(VueRouter);

const ifNotAuthenticated = (to, from, next) => {
  console.log(from.path, '-', to.path);
  if (!store.getters['user/isAuthenticated']) {
    next();
    return;
  }
  next('/login');
};

const ifAuthenticated = (to, from, next) => {
  console.log(from.path, ';', to.path);
  if (store.getters['user/isAuthenticated']) {
    next();
    return;
  }

  next('/login');
};

const routes = [
  {
    path: '/',
    name: 'DashboardHome',
    component: DashboardHome,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/',
    name: 'Table',
    component: Table,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/',
    name: 'Card',
    component: Card,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/',
    name: 'ChartCard',
    component: ChartCard,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/',
    name: 'StatsCard',
    component: StatsCard,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/home',
    name: 'Home',
    component: Home,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/add-property',
    name: 'AddProperty',
    component: AddProperty,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/add-photos',
    name: 'AddPhotos',
    component: AddPhotos,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/property-facts',
    name: 'PropertyFacts',
    component: PropertyFacts,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/authorized-users',
    name: 'Authorizations',
    component: Authorizations,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/partners',
    name: 'Partners',
    component: Partners,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/property/:id',
    name: 'AddFacts',
    component: AddFacts,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/main-facts',
    name: 'MainInfo',
    component: MainInfo,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/enviroment-facts',
    name: 'EnviromentFactsComp',
    component: EnviromentFactsComp,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/facilities-facts',
    name: 'FacilitiesFactsComp',
    component: FacilitiesFactsComp,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/rooms-facts',
    name: 'RoomsFactsComp',
    component: RoomsFactsComp,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/activities-facts',
    name: 'ActivitiesFactsComp',
    component: WellnessFactsComp,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/children-facts',
    name: 'ChildrenFactsComp',
    component: ChildrenFactsComp,
    beforeEnter: ifAuthenticated,
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
  },
  {
    path: '/auth/activate/:uid/:token',
    name: 'Activate',
    component: ActivateUser,
  },
  {
    path: '/auth/password/reset/confirm/:uid/:token',
    name: 'ResetConfirm',
    component: ResetPassword,
  }, {
    path: '/auth/account/resend/activation',
    name: 'ResendActivation',
    component: ResendActivation,
    beforeEnter: ifNotAuthenticated,
  },
  {
    path: '/login',
    name: 'Login',
    component: LoginForm,
    // beforeEnter: ifNotAuthenticated,
  },
  {
    path: '/signup',
    name: 'Signup',
    component: SignupForm,
    beforeEnter: ifNotAuthenticated,
  }, {
    path: '/reset',
    name: 'Reset',
    component: ForgotPassword,
    beforeEnter: ifNotAuthenticated,
  },
  // {
  //   // otherwise redirect to home
  //   path: '*',
  //   redirect: '/',
  // },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
