import MainInformation from './facts/MainInfo.vue';
import EnviromentFactsComp from './facts/Enviroment.vue';
import FacilitiesFactsComp from './facts/Facilities.vue';
import RoomsFactsComp from './facts/Rooms.vue';
import FoodFactsComp from './facts/FoodAndBeverage.vue';
import AIFactsComp from './facts/AllInclusive.vue';
import WellnessFactsComp from './facts/SportsWellness.vue';
import ChildrenFactsComp from './facts/Children.vue';
import EntertainmentFactsComp from './facts/Entertainment.vue';
import AdditionalFactsComp from './facts/Additional.vue';
import MaintenanceFactsComp from './facts/Maintenance.vue';

export default {
  data() {
    return {
      isLoading: false,
      currentTab: 1,
      path: MainInformation,
      EnviromentFactsComp,
      FacilitiesFactsComp,
      RoomsFactsComp,
      FoodFactsComp,
      AIFactsComp,
      WellnessFactsComp,
      ChildrenFactsComp,
      EntertainmentFactsComp,
      AdditionalFactsComp,
      MaintenanceFactsComp,
    };
  },
  components: {
    MainInformation,
    EnviromentFactsComp,
    FacilitiesFactsComp,
    RoomsFactsComp,
    FoodFactsComp,
    AIFactsComp,
    WellnessFactsComp,
    ChildrenFactsComp,
    EntertainmentFactsComp,
    AdditionalFactsComp,
    MaintenanceFactsComp,
  },
  created() {
    this.callInfo();
  },
  methods: {
    selectTab(selectedTab) {
      this.currentTab = selectedTab;
    },
    callInfo() {
      const propertyId = this.$route.params.id;
      this.isLoading = true;
      console.log(propertyId);
      this.selectTab(1);
      this.$store.dispatch('mainInfo/getInfo', propertyId)
        .then((response) => {
          console.log('my info is', response);
        }).catch((error) => {
          console.log(error);
        });
      this.isLoading = false;
    },
  },
  watch: {
    // $route() {
    //   this.callInfo();
    // },
  },
};
