  </div>
</template>
<script>
export default {
  name: 'ChildrenInformation',
  components: {

  },
  data() {
    return {

    };
  },
  computed: {

  },
  async created() {
    await this.getInfo().then(
      console.log(this.property),
      // this.retriveProperty(),
    );
  },
  methods: {
    convertTimeFormat(inputTime) {
      const date = new Date(inputTime);
      const resultDate = `${date.getUTCFullYear()}-${
        date.getUTCMonth() + 1
      }-${date.getUTCDate()}`;
      const resultTime = `${date.getUTCHours()}:${date.getUTCMinutes()}:${date.getUTCSeconds()}.${date.getUTCMilliseconds()}`;
      return `${resultDate}T${resultTime}Z`;
    },
    async getInfo() {
      const propertyId = this.$route.params.id;
      console.log('property id is', propertyId);
      this.$store.dispatch('view/load', true);
      await this.$store
        .dispatch('childrenInfo/getInfo', propertyId)
        .then((response) => {
          this.property = this.$store.getters['childrenInfo/getProperty'];
          this.$store.dispatch('view/load', false);
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
          this.$store.dispatch('view/load', false);
        });
    },
    async save() {
      if (this.isLoading) {
        return;
      }
      this.isLoading = true;
      const propertyId = this.$route.params.id;
      const childrenToUpdate = {
        id: this.property.id,
        hotel: this.property.id || propertyId,
        babyPoolAvailable: this.property.babyPoolAvailable,
        babyPoolDepth: this.property.babyPoolDepth,
        childrenPoolAvailable: this.property.childrenPoolAvailable,
        childrenPoolCount: this.property.childrenPoolCount,
        childrenPoolDepth: this.property.childrenPoolDepth,
        childrenPoolType: this.property.childrenPoolType,
        childrenPoolFrom: this.property.childrenPoolFrom,
        childrenPoolTo: this.property.childrenPoolTo,
        childrenPoolIsHeated: this.property.childrenPoolIsHeated,
        childrenPoolHasSlides: this.property.childrenPoolHasSlides,
        childrenPoolSlidesCount: this.property.childrenPoolSlidesCount,
        childrenPoolHasPirateShip: this.property.childrenPoolHasPirateShip,
        childrenPlayGroundAvailable: this.property.childrenPlayGroundAvailable,
        childrenPlayGroundCount: this.property.childrenPlayGroundCount,
        childrenPlayGroundAdventure: this.property.childrenPlayGroundAdventure,
        childrenPlayGroundParadise: this.property.childrenPlayGroundParadise,
        childrenPlayGroundCovered: this.property.childrenPlayGroundCovered,
        childrenMiniClubAvailable: this.property.childrenMiniClubAvailable,
        childrenMiniClubName: this.property.childrenMiniClubName,
        childrenMiniClubAgeFrom: this.property.childrenMiniClubAgeFrom,
        childrenMiniClubCharges: this.property.childrenMiniClubCharges,
        childrenMiniClubPrice: this.property.childrenMiniClubPrice,
        childrenMiniClubCurrency: this.property.childrenMiniClubCurrency,
        childrenMiniClubParentExist: this.property.childrenMiniClubParentExist,
        childrenMiniClubParentOnGround: this.property.childrenMiniClubParentOnGround,
        childrenMiniClubLanguage: this.property.childrenMiniClubLanguage,
        ChildrenMiniClubDays: this.property.ChildrenMiniClubDays,
        childrenMiniClubFrom: this.property.childrenMiniClubFrom,
        childrenMiniClubTo: this.property.childrenMiniClubTo,
        childrenMiniClubRestriction: this.property.childrenMiniClubRestriction,
        childrenEntertainment: this.property.childrenEntertainment,
        babyCotsAvailable: this.property.babyCotsAvailable,
        babyCotcharges: this.property.babyCotcharges,
        babyCotReservationRequired: this.property.babyCotReservationRequired,
        babyCotOnRequest: this.property.babyCotOnRequest,
        babyCotPriceUpSeason: this.property.babyCotPriceUpSeason,
        babyCotPrice: this.property.babyCotPrice,
        babysitterService: this.property.babysitterService,
        babysitterReservationRequired: this.property.babysitterReservationRequired,
        babysitterOnRequest: this.property.babysitterOnRequest,
        childrenOtherServices: this.property.childrenOtherServices,
      };
      await this.$store
        .dispatch('childrenInfo/postInfo', childrenToUpdate)
        .then((response) => {
          this.isLoading = false;
          this.property = this.$store.getters['childrenInfo/getProperty'];
          console.log(response);
        })
        .catch((error) => {
          console.log(error.data);
          this.isLoading = false;
        });
    },
  },
  watch: {
    $route() {
      this.getInfo();
    },
  },
};
</script>
