<script>
import BarsInstance from './BarsInstance.vue';
import RestaurantsInstance from './RestaurantInstance.vue';

export default {
  name: 'facilitiesFacts',
  data() {
    return {
      bars: [
        {
          barId: 0,
          isSelected: true,
        },
      ],
      restaurants: [
        {
          restaurantId: 0,
          isSelected: true,
        },
      ],
      property: this.$store.getters['facilitiesInfo/getProperty'],
      parkingCharges: [
        'Inclsuive',
        'Additional charge',
        'parking lot per unit',
      ],
      sunLoungersTypes: ['Balinese loungers', 'deckchairs', 'daybeds'],
      sunLoungersCharges: [
        'deposit required',
        'inclusive',
        'additional charges',
        'only with All inclusive',
      ],
      // parkingCharges: ['Resort', 'Hotel', 'Motel', 'Villa', 'Appartment'],
    };
  },
  components: {
    BarsInstance,
    RestaurantsInstance,
  },
  computed: {
    lastIdBar() {
      return this.bars[this.bars.length - 1].barId;
    },
    lastIdRestaurant() {
      return this.restaurants[this.restaurants.length - 1].restaurantId;
    },
  },
  methods: {
    changeTabBar(value) {
      this.bars.forEach((bar) => {
        bar.isSelected = bar.barId === value.barId;
      });
    },
    addBar() {
      const newBar = {
        barId: this.lastIdBar + 1,
        isSelected: false,
      };
      this.bars.push(newBar);
    },
    changeTabRestaurant(value) {
      this.restaurants.forEach((restaurant) => {
        restaurant.isSelected = restaurant.restaurantId === value.restaurantId;
      });
    },
    addRestaurant() {
      const newrestaurant = {
        restaurantId: this.lastIdRestaurant + 1,
        isSelected: false,
      };
      this.restaurants.push(newrestaurant);
    },
    async save() {
      if (this.isLoading) {
        return;
      }
      this.isLoading = true;
      const facilitiesToUpdate = {
        id: this.property.id,
        hotelCharacteristics: this.property.hotelCharacteristics,
        outdoorCharacteristics: this.property.outdoorCharacteristics,
