        hotelBeachSeparateRoad: this.property.hotelBeachSeparateRoad,
        quietRelaxing: this.property.quietRelaxing,
        outsideTheCity: this.property.outsideTheCity,
        hasSunLoungers: this.property.hasSunLoungers,
        sunLoungersOnBeach: this.property.sunLoungersOnBeach,
        sunLoungersBeachQuantity: this.property.sunLoungersBeachQuantity,
        sunLoungersBeachType: this.property.sunLoungersBeachType,
        sunLoungersBeachCharges: this.property.sunLoungersBeachCharges,
        sunLoungersBeachIsSeasonal: this.property.sunLoungersBeachIsSeasonal,
        sunLoungersBeachDrinkService:
          this.property.sunLoungersBeachDrinkService,
        sunLoungersOnPool: this.property.sunLoungersOnPool,
        sunLoungersPoolQuantity: this.property.sunLoungersPoolQuantity,
        sunLoungersPoolType: this.property.sunLoungersPoolType,
        sunLoungersPoolCharges: this.property.sunLoungersPoolCharges,
        sunLoungersPoolIsSeasonal: this.property.sunLoungersPoolIsSeasonal,
        sunLoungersPoolDrinkService: this.property.sunLoungersPoolDrinkService,
        sunLoungersOnGarden: this.property.sunLoungersOnGarden,
        sunLoungersGardenQuantity: this.property.sunLoungersGardenQuantity,
        sunLoungersGardenType: this.property.sunLoungersGardenType,
        sunLoungersGardenCharges: this.property.sunLoungersGardenCharges,
        sunLoungersGardenIsSeasonal: this.property.sunLoungersGardenIsSeasonal,
        sunLoungersGardenDrinkService:
          this.property.sunLoungersGardenDrinkService,
        sunLoungersOnLake: this.property.sunLoungersOnLake,
        sunLoungersLakeQuantity: this.property.sunLoungersLakeQuantity,
        sunLoungersLakeType: this.property.sunLoungersLakeType,
        sunLoungersLakeCharges: this.property.sunLoungersLakeCharges,
        sunLoungersLakeIsSeasonal: this.property.sunLoungersLakeIsSeasonal,
        sunLoungersLakeDrinkService: this.property.sunLoungersLakeDrinkService,
        sunLoungersOnSunTerrace: this.property.sunLoungersOnSunTerrace,
        sunLoungersSunTerraceQuantity:
          this.property.sunLoungersSunTerraceQuantity,
        sunLoungersSunTerraceType: this.property.sunLoungersSunTerraceType,
