        nearbyCarParkingIsGuarded: this.property.nearbyCarParkingIsGuarded,
        publicUnderGroundParkingAvailable:
          this.property.publicUnderGroundParkingAvailable,
        publicUnderGroundParkingCharges:
          this.property.publicUnderGroundParkingCharges,
        publicUnderGroundParkingPrice:
          this.property.publicUnderGroundParkingPrice,
        publicUnderGroundParkingSlotsCount:
          this.property.publicUnderGroundParkingSlotsCount,
        publicUnderGroundParkingIsGuarded:
          this.property.publicUnderGroundParkingIsGuarded,
