        hotelHasCafes: this.property.hotelHasCafes,
        hotelCafesNumber: this.property.hotelCafesNumber,
      };
      await this.$store
        .dispatch('facilitiesInfo/postInfo', facilitiesToUpdate)
        .then((response) => {
          this.isLoading = false;
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
        });
    },
  },
};
</script>
