</template>
<script>
export default {
  data() {
    return {
      loadingProperties: false,
      darkMode: false,
    };
  },
  props: {
    backgroundColor: {
      type: String,
      default: 'light',
    },
  },
  computed: {
    properties() {
      return this.$store.getters['property/getProperties'];
    },
    isAuthenticated() {
      return this.$store.getters['user/isAuthenticated'];
    },
    isLoading() {
      return this.$store.getters['view/loadStatus'];
    },
    darkDark() {
      return this.darkMode && 'darkmode-toggled';
    },
  },
  created() {
    console.log('created');
    this.getProperties();
  },
  methods: {
    async getProperties() {
      this.loadingProperties = true;
      const isAuthenticated = this.$store.getters['user/isAuthenticated'];
      if (!isAuthenticated) { return; }
      await this.$store
        .dispatch('property/getInfo')
        .then((response) => {
          console.log(response);
          this.loadingProperties = false;
        })
        .catch((error) => {
          console.log(error);
          this.loadingProperties = false;
        });
    },
    signOut() {
      this.$store.dispatch('user/destroyToken');
      this.$router.push('/login');
    },
    dark() {
      document.querySelector('body').classList.add('dark-mode');
      this.darkMode = true;
      this.$emit('dark');
    },

    light() {
      document.querySelector('body').classList.remove('dark-mode');
      this.darkMode = false;
      this.$emit('light');
    },

    modeToggle() {
      if (this.darkMode || document.querySelector('body').classList.contains('dark-mode')) {
        this.backgroundColor = 'light';
        this.light();
      } else {
        this.backgroundColor = 'dark';
        this.dark();
      }
    },
  },
};
</script>
<style scoped>
  #sign-out {
    color: orangered;
    padding: 0 !important;
    border: none;
    width: 100%;
    text-align: left;
  }

  #overlay {
    position: fixed; /* Sit on top of the page content */
    display: none; /* Hidden by default */
    width: 100%; /* Full width (cover the whole page) */
    height: 100%; /* Full height (cover the whole page) */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7); /* Black background with opacity */
    z-index: 2;
    cursor: pointer; /* Add a pointer on hover */
    display: block;
  }

  .loader {

    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
  }
  #svg-loader {
    width: 120px;
    fill: none;
  }

  .load {
    transform-origin: 50% 50%;
    stroke-dasharray: 570;
    stroke-width: 20px;
  }
  .load.one {
    stroke: orangered;
    animation: load 1.5s infinite;
  }

  .load.two {
    stroke: orange;
    animation: load 1.5s infinite;
    animation-delay: 0.1s;
  }
  .load.three {
    stroke: rgb(218, 189, 136);
    animation: load 1.5s infinite;
    animation-delay: 0.2s;
  }
  .point {
    animation: bounce 1s infinite ease-in-out;
  }

  .point.one {
    fill: rgb(218, 189, 136);
    animation-delay: 0s;
  }
  .point.two {
    fill: orange;
    animation-delay: 0.1s;
  }
  .point.three {
    fill: orangered;
    animation-delay: 0.2s;
  }

  @keyframes bounce {
    0%,
    100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-20px);
    }
  }
  @keyframes load {
    0% {
      stroke-dashoffset: 570;
    }
    50% {
      stroke-dashoffset: 530;
    }
    100% {
      stroke-dashoffset: 570;
      transform: rotate(360deg);
    }
  }
  .router-link-exact-active {
    font-weight: 550 !important;
    color: rgb(255, 255, 255) !important;
    background-color: rgb(235, 152, 0) !important;
  }

  @import "../node_modules/@syncfusion/ej2-base/styles/bootstrap4.css";
  @import "../node_modules/@syncfusion/ej2-inputs/styles/bootstrap4.css";
  @import "../node_modules/@syncfusion/ej2-vue-dropdowns/styles/bootstrap4.css";
  @import "../node_modules/@syncfusion/ej2-buttons/styles/bootstrap4.css";

</style>
