<template>
  <div class="container">
    <form v-on:submit.prevent @submit="save()">
      <h2>Main Info</h2>
      <!-- main info -->
      <b-row>
        <b-col cols="4">
          <label>Hotel Name</label>
          <input
            v-model.trim="property.hotelName"
            type="text"
            class="form-control"
            disabled
         >
        </b-col>
        <b-col cols="2">
          <label>Accommodation Type <span class="text-danger">* </span>
          <select
            class="form-control"
            name="country"
            v-model="property.accommodationType"
            required>
            <option value="null" selected>--Select Type--</option>
            <option
              v-for="(a, index) in accommodationTypes"
              :key="index"
              :value="a">
              {{ a }}
            </option>
          </select>
          </label>
        </b-col>
        <b-col cols="2">
          <label>Rating <span class="text-danger">* </span>
          <select class="form-control" v-model="property.rating" required>
            <option value="0" selected>--Select Rating--</option>
            <option value="5">&#9733;&#9733;&#9733;&#9733;&#9733;</option>
            <option value="4.5">&#9733;&#9733;&#9733;&#9733;&#189;</option>
            <option value="4">&#9733;&#9733;&#9733;&#9733;</option>
            <option value="3.5">&#9733;&#9733;&#9733;&#189;</option>
            <option value="3">&#9733;&#9733;&#9733;</option>
            <option value="2">&#9733;&#9733;</option>
            <option value="1">&#9733;</option>
          </select>
          </label>
        </b-col>
      </b-row>
      <hr>
      <!-- address info  -->
      <b-row>
        <b-col cols="2">
          <label>Country</label>
          <select class="form-control" name="country"
            v-model="property.country" disabled>
          <option value="" selected>--Select Country--</option>
          <option
            v-for="(k, index) in countries"
            :key="index" :value="index">
            {{ k }}
          </option>
        </select>
        </b-col>
        <b-col cols="2">
          <label>City</label>
          <input
            v-model.trim="property.city"
            type="text"
            class="form-control"
            disabled
         >
        </b-col>
        <b-col cols="2">
          <label>District</label>
          <input
            v-model.trim="property.district"
            type="text"
            class="form-control"
            disabled
         >
        </b-col>
        <b-col>
          <label>Address <span class="text-danger">* </span></label>
          <input
            v-model.trim="property.address"
            type="text"
            class="form-control"
            required>
        </b-col>
      </b-row>
      <hr>
      <!-- contacts info -->
      <b-row>
        <b-col cols="4">
          <label>Email <span class="text-danger">*</span></label>
          <input
            v-model.trim="property.email"
            type="email"
            class="form-control"
            required>
        </b-col>
        <b-col>
          <label>Phone <span class="text-danger">* </span>
          <input
            v-model.trim="property.phone"
            type="tel"
            class="form-control"
            required>
            </label>
        </b-col>
        <b-col>
          <label>Zip Code <span class="text-danger">* </span>
          <input
            v-model.trim="property.zipCode"
            type="text"
            class="form-control"
            required>
            </label>
        </b-col>
        <b-col>
          <label>Fax</label>
          <input v-model.trim="property.fax" type="tel" class="form-control">
        </b-col>
        <b-col>
          <label>Longitude</label>
          <input
            v-model.trim="property.longitude"
            type="double"
            style="min-width:120px;"
            class="form-control">
        </b-col>
        <b-col>
          <label>Latitude</label>
          <input
            v-model.trim="property.latitude"
            type="double"
            style="min-width:120px;"
            class="form-control">
        </b-col>
      </b-row>
      <hr>
      <!-- pets -->
      <b-row>
        <b-col cols="2" class="text-left">
          <label>Pets allowed:
            <input type="checkbox" v-model="property.petsAllowed">
          </label>
        </b-col>
      <b-col cols="10"  v-if="property.petsAllowed">
        <b-row>
          <b-col cols="3">
            <label class="ananas-bold">Charges <span class="text-danger">* </span>
            <select class="form-pine" v-model="property.petsCharges" required>
              <option value="" selected>--Select Rating--</option>
              <option value="Inclusive">Inclusive</option>
              <option value="Additional Charge">Additional Charge</option>
            </select>
            </label>
          </b-col>
            <b-col cols="2" v-if="property.petsCharges === 'Additional Charge'">
              <label class="ananas-bold">Price <span class="text-danger">* </span>
              <input
                class="form-pine"
                type="number"
                v-model="property.petschargesPrice"
                min="0"
                step="0.1"
                required>
                </label>
            </b-col>
            <b-col cols="3" v-if="property.petschargesPrice >= '0.1'">
              <label>currency <span class="text-danger">* </span>
              <select
                class="form-pine"
                v-model="property.petsChargeCurrency"
                required>
                <option value="null" selected>Select currency</option>
                <option value="USD">USD</option>
                <option value="EUR">EUR</option>
              </select>
              </label>
            </b-col>
        </b-row>
      </b-col>
      </b-row>
      <hr>
      <!-- Check in data -->
      <b-row>
        <b-col cols="2" lg="auto" md="auto">
          <label
            >Minimum Age Allowed:
            <input
              v-model.trim="property.minimumAgeAllowed"
              type="number"
              max="120"
              min="0"
              class="form-pine"
         ></label>
        </b-col>
        <b-col cols="2">
          <label
            >Check In :
            <input
              class="form-"
              type="time"
              v-model="property.checkInTime"
              placeholder="12:00"
           >
          </label>
        </b-col>
        <b-col cols="2">
          <label
            >Check Out :
            <input
              class="form-"
              type="time"
              v-model="property.checkOutTime">
          </label>
        </b-col>
      </b-row>
      <br>
      <b-row>
        <b-col cols="3" md="auto" lg="auto">
          <input
            class="form-pine"
            type="checkbox"
            v-model="property.checkInPartnerHotel">
          <span> Check-in in partner hotel</span>
        </b-col>
        <!-- partner hotel data -->
        <b-row v-if="property.checkInPartnerHotel">
          <b-col lg="auto" md="auto">
            <span class="ananas-bold">Partner Hotel name <span class="text-danger">* </span>
              <input
                class=""
                type="text"
                v-model="property.partnerHotelName"
                required
           ></span>
          </b-col>
          <b-col cols="3" lg="auto" md="auto">
            <span
              >Distance <span class="text-danger">* </span>
              <input
                class=""
                type="number"
                v-model="property.partnerHotelDistance"
                placeholder="5"
                required></span>
          </b-col>
          <b-col lg="auto" md="auto">
            <span>Distance unit </span><span class="text-danger">* </span>
            <select
              class=""
              v-model="property.partnerHotelDistanceUnit"
              required>
              <option value="null">Select type</option>
              <option value="meters">meters</option>
              <option value="minutes">minutes</option>
            </select>
          </b-col>
        </b-row>
      </b-row>
      <hr>
      <!-- prohibited alcohol in holy days -->
      <b-row>
        <b-col cols="8">
          <label> Serving alcohol is prohobited on special holy days
          <input
            class="form-pine"
            type="checkbox"
            v-model="property.alcoholProhibitedOnSpecialHolidays">
            </label>
        </b-col>
      </b-row>
      <hr>
      <!-- Other charges  -->
      <b-row>
        <b-col cols="2">
          <label
            >Deposit required:
            <input
              class="form-pine"
              type="checkbox"
              v-model="property.depositRequired"
              value="1">
          </label>
        </b-col>
        <b-col cols="10" v-if="property.depositRequired">
          <label
            >deposit required for:
            <input
              class="form-"
              type="text"
              v-model="property.depositRequiredFor"
              placeholder="Deposit for..">
              </label>
        </b-col>
      </b-row>
      <hr>
      <b-row>
        <b-col cols="2">
            <input
              class="form-pine"
              type="checkbox"
              v-model="property.tax">
              <span> Tax applicable</span>
        </b-col>
        <b-col cols="10">
          <b-row v-if="property.tax">
            <b-col cols="3">
              <label for=""
                >Charges <span class="text-danger">* </span>
                <select
                  class="form-"
                  v-model="property.taxCharges"
                  required>
                  <option value="null" selected>Select Option</option>
                  <option value="Inclusive">Inclusive</option>
                  <option value="Additional Charge">Additional Charge</option>
                </select>
                </label>
            </b-col>
            <div v-if="property.taxCharges === 'Additional Charge'">
              <b-col cols="4">
                <label>charges per person/day
                  <span class="text-danger">* </span>
                <input
                  class=""
                  type="text"
                  v-model="property.chargesPersonPerNight"
                  placeholder="14"
                  required>
                  </label>
              </b-col>
              <b-col cols="3">
                <label>currency <span class="text-danger">* </span>
                <select
                  class=""
                  v-model="property.taxCurrency"
                  required>
                  <option value="null" selected>Select currency</option>
                  <option value="USD">USD</option>
                  <option value="EUR">EUR</option>
                </select>
                </label>
              </b-col>
            </div>
          </b-row></b-col>
      </b-row>
      <hr>
      <!-- tourist tax -->
      <b-row>
        <b-col cols="3">
            <input
              class=""
              type="checkbox"
              v-model="property.touristTax"
              value="1">
              <span> Tourist tax applicable</span>
        </b-col>
        <b-row cols="" v-if="property.touristTax">
          <b-col cols="2" md="auto" lg="auto">
            <label for="" Name="tax-charges">
              Charges <span class="text-danger">* </span>
            </label>
            <select
              class=""
              v-model="property.touristCharges"
              required>
              <option value="null" selected>Select Option</option>
              <option value="Inclusive">Inclusive</option>
              <option value="Additional Charge">Additional Charge</option>
            </select>
          </b-col>
          <div v-if="property.touristCharges === 'Additional Charge'">
            <b-col cols="2" md="auto" lg="auto">
              <label
                >charges per person/day:
                <span class="text-danger">* </span>
              <input
                class=""
                type="text"
                v-model="property.touristChargesPersonPerNight"
                placeholder="14">
                </label>
            </b-col>
            <b-col cols="2" md="auto" lg="auto">
              <label>currency: <span class="text-danger">* </span>
              <select
                class=""
                v-model="property.touristCurrency">
                <option value="null" selected>Select currency</option>
                <option value="USD">USD</option>
                <option value="EUR">EUR</option>
              </select>
              </label>
            </b-col>
          </div>
        </b-row>
      </b-row>
      <hr>
      <!-- children tax -->
      <b-row>
        <b-col cols="3" md="auto" lg="auto">
            <input
              class="form-pine"
              type="checkbox"
              v-model="property.childTax"
              value="1">
              <span> Children tax</span>
        </b-col>
        <b-row cols="" v-if="property.childTax">
          <b-col cols="2" md="3" lg="auto">
            <label for="id_Children_Tax_Charges">
              Charges: <span class="text-danger">* </span>
            </label>
            <select
              class=""
              v-model="property.childCharges"
              required>
              <option value="null" selected>Select Option</option>
              <option value="Inclusive">Inclusive</option>
              <option value="Additional Charge">Additional Charge</option>
            </select>
          </b-col>
          <div v-if="property.childCharges === 'Additional Charge'">
            <b-col cols="2" md="auto" lg="auto">
              <label>charges per person/day:
                <span class="text-danger">* </span>
              <input
                class=""
                type="text"
                v-model="property.childChargesPerNight"
                placeholder="14">
                </label>
            </b-col>
            <b-col cols="2" md="auto" lg="auto">
              <label>currency: <span class="text-danger">* </span>
              <select
                class=""
                v-model="property.childCurrency"
                required>
                <option value="null" selected>Select currency</option>
                <option value="USD">USD</option>
                <option value="EUR">EUR</option>
              </select>
              </label>
            </b-col>
          </div>
          <b-col cols="2" md="auto" lg="auto">
            Age from: <span class="text-danger">* </span>
            <input
              class=""
              type="number"
              v-model="property.childTaxFrom"
              placeholder="4"
              required>
          </b-col>
          <b-col cols="2" md="auto" lg="auto">
            Age to <span class="text-danger">* </span>
            <input
              class=""
              type="number"
              v-model="property.childTaxTo"
              placeholder="16"
              required
           >
          </b-col>
        </b-row>
      </b-row>
      <hr>
      <!-- resort fees -->
      <b-row>
        <b-col cols="2" md="auto" lg="auto">
          <input
            class="form-pine"
            type="checkbox"
            v-model="property.resortFees">
            <span> Resort fees applicable</span>
        </b-col>
        <b-row v-if="property.resortFees">
          <b-col cols="2" md="auto" lg="auto">
            <label Name="tax-charges">
              Charges <span class="text-danger">* </span>
            </label>
            <select
              class=""
              v-model="property.resortFeesCharges"
              required>
              <option value="null" selected>Select Option</option>
              <option value="Inclusive">Inclusive</option>
              <option value="Additional Charge">Additional Charge</option>
            </select>
          </b-col>
          <div v-if="property.resortFeesCharges === 'Additional Charge'">
            <b-col cols="2" md="auto" lg="auto">
              <label
                >charges per person/day: <span class="text-danger">* </span>
                <input
                  class=""
                  type="number"
                  v-model="property.resortFeesChargesPerNight"
                  placeholder="10.5"
                  required>
              </label>
            </b-col>
            <b-col cols="2" md="auto" lg="auto">
              <label
                >currency: <span class="text-danger">* </span>
                <select
                  class=""
                  v-model="property.resortFeesCurrency"
                  required>
                  <option value="null" selected>Select currency</option>
                  <option value="USD">USD</option>
                  <option value="EUR">EUR</option>
                </select></label
              >
            </b-col>
          </div>
        </b-row>
      </b-row>
      <hr>
      <b-row>
        <b-col cols="2">
          <label>Registeration fees :
            <input
              class="form-pine"
              type="checkbox"
              v-model="property.registrationFee"
           >
          </label>
        </b-col>
        <b-col cols="2">
          <label>Late check-out :
            <input
              class="form-pine"
              type="checkbox"
              v-model="property.lateCheckOut">
          </label>
        </b-col>
        <b-col cols="2" v-if="property.lateCheckOut">
          <label>Price
          <input
            type="number"
            placeholder="14.00"
            step="0.1"
            min="0"
            v-model="property.lateCheckOutPrice">
            </label>
        </b-col>
      </b-row>
      <button type="submit" class="btn btn-primary btn-lg">
        SAVE
        <span v-if="isLoading" class="spinner-border"></span>
      </button>
    </form>
  </div>
</template>

<script>

export default {
  name: 'MainInformation',
  components: {
  },
  data() {
    return {
      value: null,
      options: ['list', 'of', 'options'],
      isLoading: false,
      countries: this.$store.getters['countries/getCountries'],
      property: this.$store.getters['mainInfo/getProperty'],
      accommodationTypes: ['Resort', 'Hotel', 'Motel', 'Villa', 'Appartment'],
    };
  },
  computed: {
    cPetsCharges() {
      return this.property.petsAllowed ? this.property.petsCharges : null;
    },
    cPetsPrice() {
      return this.property.petsAllowed
        && this.property.petsCharges === 'Additional Charge'
        ? this.property.petschargesPrice
        : null;
    },
    cPetsCurrency() {
      return this.property.petsAllowed
        && this.property.petsCharges === 'Additional Charge'
        ? this.property.petsChargeCurrency
        : null;
    },
    cPartnerHotel() {
      return this.property.checkInPartnerHotel
        ? this.property.partnerHotelName
        : null;
    },
    cPartnerHotelDistance() {
      return this.property.checkInPartnerHotel
        ? this.property.partnerHotelDistance
        : null;
    },
    cPartnerHotelDistnaceUnit() {
      return this.property.checkInPartnerHotel
        ? this.property.cPartnerHotelDistnaceUnit
        : null;
    },
    cDepositRequiredFor() {
      return this.property.depositRequired
        ? this.property.depositRequiredFor
        : null;
    },
    cTaxCharges() {
      return this.property.tax ? this.property.taxCharges : null;
    },
    cTaxChargesPerNight() {
      return this.property.tax
        && this.property.taxCharges === 'Additional Charge'
        ? this.property.chargesPersonPerNight
        : null;
    },
    cTaxCurrency() {
      return this.property.tax
        && this.property.taxCharges === 'Additional Charge'
        ? this.property.taxCurrency
        : null;
    },
    cTouristTaxCharges() {
      return this.property.touristTax ? this.property.touristCharges : null;
    },
    cTouristTaxChargesPerNight() {
      return this.property.touristTax
        && this.property.touristCharges === 'Additional Charge'
        ? this.property.touristChargesPersonPerNight
        : null;
    },
    cTouristTaxCurrency() {
      return this.property.touristTax
        && this.property.touristCharges === 'Additional Charge'
        ? this.property.touristCurrency
        : null;
    },
    cChildTaxCharges() {
      return this.property.childTax ? this.property.childCharges : null;
    },
    cChildTaxChargesPerNight() {
      return this.property.childTax
        && this.property.childCharges === 'Additional Charge'
        ? this.property.childChargesPerNight
        : null;
    },
    cChildTaxCurrency() {
      return this.property.childTax
        && this.property.childCharges === 'Additional Charge'
        ? this.property.childCurrency
        : null;
    },
    cResortCharges() {
      return this.property.resortFees ? this.property.resortFeesCharges : null;
    },
    cResortChargesPerNight() {
      return this.property.resortFees
        && this.property.resortFeesCharges === 'Additional Charge'
        ? this.property.resortFeesChargesPerNight
        : null;
    },
    cResortCurrency() {
      return this.property.resortFees
        && this.property.resortFeesCharges === 'Additional Charge'
        ? this.property.resortFeesCurrency
        : null;
    },
    cLateCheckoutPrice() {
      return this.property.lateCheckOut
        ? this.property.lateCheckOutPrice
        : null;
    },
  },
  async created() {
    await this.getInfo().then(
      console.log(this.property),
      // this.retriveProperty(),
    );
  },
  methods: {
    convertTimeFormat(inputTime) {
      const date = new Date(inputTime);
      const resultDate = `${date.getUTCFullYear()}-${
        date.getUTCMonth() + 1
      }-${date.getUTCDate()}`;
      const resultTime = `${date.getUTCHours()}:${date.getUTCMinutes()}:${date.getUTCSeconds()}.${date.getUTCMilliseconds()}`;
      return `${resultDate}T${resultTime}Z`;
    },
    async getInfo() {
      const propertyId = this.$route.params.id;
      console.log('property id is', propertyId);
      this.$store.dispatch('view/load', true);
      await this.$store
        .dispatch('mainInfo/getInfo', propertyId)
        .then((response) => {
          this.property = this.$store.getters['mainInfo/getProperty'];
          this.$store.dispatch('view/load', false);
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
          this.$store.dispatch('view/load', false);
        });
    },
    async save() {
      if (this.isLoading) {
        return;
      }
      this.isLoading = true;
      const propertyId = this.$route.params.id;
      const infoToUpdate = {
        id: this.property.id,
        hotel: this.property.id || propertyId,
        hotel_name: this.property.hotelName,
        accommodation_type: this.property.accommodationType,
        rating: this.property.rating,
        country: this.property.country,
        city: this.property.city,
        district: this.property.district,
        address: this.property.address,
        email: this.property.email,
        phone: this.property.phone,
        zip_code: this.property.zipCode,
        fax: this.property.fax,
        longitude: this.property.longitude,
        latitude: this.property.latitude,
        pets_allowed: this.property.petsAllowed,
        pets_charges: this.cPetsCharges,
        pets_price: this.cPetsPrice,
        pets_currency: this.cPetsCurrency,
        minimum_age_allowed: this.property.minimumAgeAllowed,
        check_in_time: this.property.checkInTime,
        check_out_time: this.property.checkOutTime,
        check_in_partner_hotel: this.property.checkInPartnerHotel,
        partner_hotel: this.cPartnerHotel,
        partner_hotel_distance: this.cPartnerHotelDistance,
        partner_hotel_distance_unit: this.cPartnerHotelDistnaceUnit,
        alcohol_prohibited_on_special_holidays:
          this.property.alcoholProhibitedOnSpecialHolidays,
        deposit_required: this.property.depositRequired,
        deposit_required_for: this.cDepositRequiredFor,
        tax: this.property.tax,
        tax_charges: this.cTaxCharges,
        charges_person_per_night: this.cTaxChargesPerNight,
        tax_currency: this.cTaxCurrency,
        tourist_tax: this.property.touristTax,
        tourist_charges: this.cTouristTaxCharges,
        tourist_charges_person_per_night: this.cTouristTaxChargesPerNight,
        tourist_currency: this.cTouristTaxCurrency,
        child_tax: this.property.childTax,
        child_charges: this.cChildTaxCharges,
        child_charges_per_night: this.cChildTaxChargesPerNight,
        child_currency: this.cChildTaxCurrency,
        age_from: this.property.childTaxFrom,
        age_to: this.property.childTaxTo,
        resort_fee: this.property.resortFees,
        resort_charges: this.cResortCharges,
        resort_charges_per_night: this.cResortChargesPerNight,
        resort_currency: this.cResortCurrency,
        registration_fee: this.property.registrationFee,
        late_check_out: this.property.lateCheckOut,
        late_check_out_price: this.cLateCheckoutPrice,
      };
      await this.$store
        .dispatch('mainInfo/postInfo', infoToUpdate)
        .then((response) => {
          this.isLoading = false;
          this.property = this.$store.getters['mainInfo/getProperty'];
          console.log(response);
        })
        .catch((error) => {
          console.log(error.data);
          this.isLoading = false;
        });
    },
  },
  watch: {
    $route() {
      this.getInfo();
    },
  },
};
</script>
