<template>
  <div class="container">
    <h3>Maintenances</h3>
    <b-row>
      <b-col cols="12">
        <p>
          <button
            class="pull-right" @click="addMaintenance()">Add Maintenance</button>
        </p>
      </b-col>
    </b-row>
    <!-- this is container of all rooms  -->
    <b-row>
      <b-col cols="12">
        <b-tabs card>
          <!-- this is a room tab, each room must be in a separate tab
      with the name of the room instead of Tab 1 -->

          <!-- End room data -->
          <div v-for="(r, index) of Maintenances" :key="index">
            <MaintenanceInstance
              :MaintenanceId="r.MaintenanceId"
              :isSelected="r.isSelected"
              @activated="changeTab"/>
          </div>
        </b-tabs>
        </b-col>
    </b-row>
  </div>
</template>
<script>
import MaintenanceInstance from './MaintenanceInstance.vue';

export default {
  data() {
    return {
      Maintenances: [{
        MaintenanceId: 0,
        isSelected: true,
      }],
    };
  },
  components: {
    MaintenanceInstance,
  },
  computed: {
    lastId() {
      return this.Maintenances[this.Maintenances.length - 1].MaintenanceId;
    },
  },
  methods: {
    changeTab(value) {
      this.Maintenances.forEach((Maintenance) => {
        Maintenance.isSelected = Maintenance.MaintenanceId === value.MaintenanceId;
      });
    },
    addMaintenance() {
      const newMaintenance = {
        MaintenanceId: this.lastId + 1,
        isSelected: false,
      };
      this.Maintenances.push(newMaintenance);
    },
    async save() {
      if (this.isLoading) {
        return;
      }
      this.isLoading = true;
      const RoomsToUpdate = {
        id: this.property.id,

      };
      await this.$store
        .dispatch('roomsInfo/postInfo', RoomsToUpdate)
        .then((response) => {
          this.isLoading = false;
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
        });
    },
  },
};
</script>
