<template>
  <div>
    <form v-on:submit.prevent @submit="save()">
      <slot>
        <div id="Location" class="container">
          <h3>Location & Enviroment:</h3>
          <hr>
          <!-- Location -->
          <b-row>
            <b-col cols="4">
          <label>Location Enviroment Type</label>
          <ejs-multiselect
            v-model="property.locationEnviroment"
            :dataSource='locationEnviromentOptions'
            placeholder="please select..."
            mode="CheckBox"
            :showSelectAll='true'
            selectAllText="Select All"
            unSelectAllText="Unselect All"
            showDropDownIcon="true"
            ></ejs-multiselect>
          </b-col>
        </b-row>
          <b-row>
            <b-col cols="4">
          <label>Around Hotel</label>
          <ejs-multiselect
            v-model="property.aroundHotel"
            :dataSource='aroundHotelOptions'
            placeholder="please select..."
            mode="CheckBox"
            :showSelectAll='true'
            selectAllText="Select All"
            unSelectAllText="Unselect All"
            showDropDownIcon="true"
            ></ejs-multiselect>
          </b-col>
        </b-row>
          <hr>
          <!-- Beach section -->
          <b-row>
            <b-col cols="3">
              <label class="checkboxcontainernofont">
                <input
                  class="form-pine"
                  type="checkbox"
                  v-model="property.beachAvail">
                Beach Available<span class="checkmark"></span>
                </label>
            </b-col>
            </b-row>
            <b-row>
            <b-col cols="12">
              <b-row v-if="property.beachAvail">
                <b-col cols="12">
                  <label>Beach characteristics</label>
                  <ejs-multiselect
                    v-model="property.beachCharacteristics"
                    :dataSource='beachCharacteristicsOptions'
                    placeholder="please select..."
                    mode="CheckBox"
                    :showSelectAll='true'
                    selectAllText="Select All"
                    unSelectAllText="Unselect All"
                    showDropDownIcon="true"
                    ></ejs-multiselect>
                </b-col>
                <b-col cols="4">
                  <label>Distance to beach
                  <input
                    class="form-pine"
                    type="number"
                    v-model="property.distanceToBeach">
                  </label>
                </b-col>
                <b-col cols="4">
                  <label>unit
                    <select
                      class="form-pine"
                      v-model="property.distanceToBeachUnit">
                      <option value="">Select measurment</option>
                      <option value="Meters">Meters</option>
                      <option value="Minutes">Minutes</option>
                      <option value="Km">Km</option>
                    </select>
                    </label>
                  </b-col>
                  <b-col cols="12">
                  <div>
                  <label class="checkboxcontainer">Private Beach
                    <input
                      class="form-pine"
                      type="checkbox"
                      v-model="property.beachIsPrivate"><br>
                    <span class="checkmark"></span>
                    </label>
                  <label class="checkboxcontainer">Nude Beach
                    <input
                      class="form-pine"
                      type="checkbox"
                      v-model="property.beachIsNude"><br>
                    <span class="checkmark"></span>
                    </label>
                  </div>
                  </b-col>
                  <b-col cols="6">
                  <label>Beach More Info:</label>
                  <ejs-multiselect
                    v-model="property.moreBeachInfo"
                    :dataSource='moreBeachInfoOptions'
                    placeholder="please select..."
                    mode="CheckBox"
                    :showSelectAll='true'
                    selectAllText="Select All"
                    unSelectAllText="Unselect All"
                    showDropDownIcon="true"
                    ></ejs-multiselect>
                  <br>
                  <label>Beach info:</label>
                  <ejs-multiselect
                    v-model="property.beachInformation"
                    :dataSource='beachInformationOptions'
                    placeholder="please select..."
                    mode="CheckBox"
                    :showSelectAll='true'
                    selectAllText="Select All"
                    unSelectAllText="Unselect All"
                    showDropDownIcon="true"
                    ></ejs-multiselect>
                  <br>
                  <label>Beach Path:</label>
                  <ejs-multiselect
                    v-model="property.beachPathType"
                    :dataSource='beachPathTypeOptions'
                    placeholder="please select..."
                    mode="CheckBox"
                    :showSelectAll='true'
                    selectAllText="Select All"
                    unSelectAllText="Unselect All"
                    showDropDownIcon="true"
                    ></ejs-multiselect>
                </b-col>
              </b-row>
            </b-col>
          </b-row>
          <!-- Beach end -->
          <hr>
          <!-- Airport -->
          <b-row>
            <b-col cols="5">
              <label class="ananas-bold"
                >Closest Airport
                <span class="text-danger"> * </span>
              </label>
              <input
                class="form-pine"
                type="text"
                v-model="property.closestAirport"
                value="">
            </b-col>
            <div class="col-pine-3">
              <label>Distance:</label>
              <input
                class="form-pine"
                type="number"
                v-model="property.distanceToAirport">
            </div>
            <div>
              <label>unit</label>
              <select
                class="form-pine"
                v-model="property.airportDistanceType">
                <option value="">Select measurment</option>
                <option value="KM">KM</option>
                <option value="Mile">Mile</option>
                <option value="Minutes">Minutes</option>
                <option value="Hours">Hours</option>
              </select>
            </div>
          </b-row>
          <hr>
          <!-- Trains -->
          <b-row>
            <b-col cols="3">
              <label class="checkboxcontainernofont ananas-bold"
                >Train Availability:
                <input
                  class="form-pine"
                  type="checkbox"
                  v-model="property.trainAvailable"
                  value="1"><br>
                <span class="checkmark"></span>
                </label>
            </b-col>
            <b-col cols="9" v-if="property.trainAvailable">
              <b-row>
                <div class="col-pine-3">
                  <label>Station name</label>
                  <input
                    class="form-pine"
                    type="text"
                    v-model="property.trainStationName"
                    value="">
                </div>
                <div class="col-pine-3">
                  <label>Distance:</label>
                  <input
                    class="form-pine"
                    type="number"
                    v-model="property.distanceToTrain"
                    value="">
                </div>
                <div class="col-pine-3">
                  <label>unit</label>
                  <select
                    class="form-pine"
                    v-model="property.trainDistanceUnit">
                    <option value="">Select measurment</option>
                    <option value="KM">KM</option>
                    <option value="Mile">Mile</option>
                    <option value="Minutes">Minutes</option>
                    <option value="Hours">Hours</option>
                  </select>
                </div>
              </b-row>
            </b-col>
          </b-row>
          <hr>
          <!-- distance to facilities outside the hotel -->
          <b-row>
            <b-col cols="4">
              <label>To shopping area:</label>
            </b-col>
            <b-col cols="4">
              <label>distance:</label>
              <input
                class="form-pine"
                type="number"
                v-model="property.distanceToShopping">
            </b-col>
            <b-col cols="4">
              <label>unit</label>
              <select
                class="form-pine"
                v-model="property.distanceToShoppingUnit">
                <option value="">Select measurment</option>
                <option value="KM">KM</option>
                <option value="Mile">Mile</option>
                <option value="Minutes">Minutes</option>
                <option value="Hours">Hours</option>
              </select>
            </b-col>
          </b-row>
          <hr>
          <b-row>
            <b-col cols="4">
              <label>To entertainment options:</label>
            </b-col>
            <b-col cols="4">
              <label>distance:</label>
              <input
                class="form-pine"
                type="number"
                v-model="property.distanceToEntertainment">
            </b-col>
            <b-col cols="4">
              <label>unit</label>
              <select
                class="form-pine"
                v-model="property.distanceToEntertainmentUnit">
                <option value="">Select measurment</option>
                <option value="KM">KM</option>
                <option value="Mile">Mile</option>
                <option value="Minutes">Minutes</option>
                <option value="Hours">Hours</option>
              </select>
            </b-col>
          </b-row>
          <hr>
          <b-row>
            <b-col cols="4">
              <label>To restaurants & bars:</label>
            </b-col>
            <b-col cols="4">
              <label>distance:</label>
              <input
                class="form-pine"
                type="number"
                v-model="property.distanceToRestaurants"
                value="">
            </b-col>
            <b-col cols="4">
              <label>unit</label>
              <select
                class="form-pine"
                v-model="property.distanceToRestaurantsUnit">
                <option value="">Select measurment</option>
                <option value="KM">KM</option>
                <option value="Mile">Mile</option>
                <option value="Minutes">Minutes</option>
                <option value="Hours">Hours</option>
              </select>
            </b-col>
          </b-row>
          <hr>
          <b-row>
            <b-col cols="4">
              <label>To city center:</label>
            </b-col>
            <b-col cols="4">
              <label>distance:</label>
              <input
                class="form-pine"
                type="number"
                v-model="property.distanceToCityCenter"
                value=""
             >
            </b-col>
            <b-col cols="4">
              <label>unit</label>
              <select
                class="form-pine"
                v-model="property.distanceToCityCenterUnit"
              >
                <option value="">Select measurment</option>
                <option value="KM">KM</option>
                <option value="Mile">Mile</option>
                <option value="Minutes">Minutes</option>
                <option value="Hours">Hours</option>
              </select>
            </b-col>
          </b-row>
          <hr>
          <b-row>
            <b-col cols="4">
              <label>To bus stop:</label>
            </b-col>
            <b-col cols="4">
              <label>distance:</label>
              <input
                class="form-pine"
                type="number"
                v-model="property.distanceToBusStop"
             >
            </b-col>
            <b-col cols="4">
              <label>unit</label>
              <select
                class="form-pine"
                v-model="property.distanceToBusStopUnit"
              >
                <option value="">Select measurment</option>
                <option value="KM">KM</option>
                <option value="Mile">Mile</option>
                <option value="Minutes">Minutes</option>
                <option value="Hours">Hours</option>
              </select>
            </b-col>
          </b-row>
          <hr>
          <!-- local transfer section -->
          <b-row>
            <b-col cols="2">
              <label class="checkboxcontainernofont"
                >domestic flight:
                <input
                  class="form-pine"
                  type="checkbox"
                  v-model="property.domesticFlightAvail"
                  value="1"><br><span class="checkmark"></span
              ></label>
            </b-col>
            <div v-if="property.domesticFlightAvail">
              <label for=""
                >duration in minutes (approx.)
                <input
                  class="form-pine"
                  type="number"
                  v-model="property.domesticFlightDuration"
               >
              </label>
            </div>
            <div v-if="property.domesticFlightAvail">
              <label for=""
                >supplier
                <input
                  class="form-pine"
                  type="text"
                  v-model="property.domesticFlightSupplier"
               >
              </label>
            </div>
          </b-row>
          <b-row>
            <b-col cols="2">
              <label class="checkboxcontainernofont"
                >speed boat
                <input
                  class="form-pine"
                  type="checkbox"
                  v-model="property.speedBoatAvail"
                  value="1"><br><span class="checkmark"></span
              ></label>
            </b-col>
            <div v-if="property.speedBoatAvail">
              <label for=""
                >duration in minutes (approx.)
                <input
                  class="form-pine"
                  type="number"
                  v-model="property.speedBoatDuration"
             ></label>
            </div>
            <div v-if="property.speedBoatAvail">
              <label for=""
                >supplier
                <input
                  class="form-pine"
                  type="text"
                  v-model="property.speedBoatSupplier"
             ></label>
            </div>
          </b-row>
          <b-row>
            <b-col cols="2">
              <label class="checkboxcontainernofont"
                >seaplane
                <input
                  class="form-pine"
                  type="checkbox"
                  v-model="property.seaPlaneAvail"
                  value="1"><br><span class="checkmark"> </span
              ></label>
            </b-col>
            <div v-if="property.seaPlaneAvail">
              <label for=""
                >duration in minutes (approx.)
                <input
                  class="form-pine"
                  type="number"
                  v-model="property.seaPlaneDuration"
               >
              </label>
            </div>
            <div v-if="property.seaPlaneAvail">
              <label for=""
                >supplier
                <input
                  class="form-pine"
                  type="text"
                  v-model="property.seaPlaneSupplier"
             ></label>
            </div>
          </b-row>
          <b-row>
            <b-col cols="2">
              <label class="checkboxcontainernofont"
                >Other
                <input
                  class="form-pine"
                  type="checkbox"
                  v-model="property.otherDomesticTrans">
                <br><span class="checkmark"> </span
              ></label>
            </b-col>
            <div v-show="property.otherDomesticTrans">
              <label for=""
                >type of transfer
                <input
                  class="form-pine"
                  type="text"
                  v-model="property.otherDomesticTransName"
                  value=""
             ></label>
            </div>
            <div v-show="property.otherDomesticTrans">
              <label for=""
                >duration in minutes (approx.)
                <input
                  class="form-pine"
                  type="number"
                  v-model="property.otherDomesticTransDuration"
                  value=""
             ></label>
            </div>
            <div v-show="property.otherDomesticTrans">
              <label for=""
                >supplier
                <input
                  class="form-pine"
                  type="text"
                  v-model="property.otherDomesticTransSupplier"
                  value=""
             ></label>
            </div>
          </b-row>
          <hr>
        </div>
      </slot>
      <button type="submit" class="btn btn-primary btn-lg">
        SAVE
        <span v-if="isLoading" class="spinner-border"></span>
      </button>
    </form>
  </div>
</template>
<script>
export default {
  name: 'EnviromentFactsComp',
  data() {
    return {
      isLoading: false,
      property: this.$store.getters['enviromentFactsComp/getProperty'],
    };
  },
  computed: {
    locationEnviromentOptions() {
      return this.$store.getters['options/getLocationEnviroment'];
    },
    aroundHotelOptions() {
      return this.$store.getters['options/getAroundHotel'];
    },
    beachCharacteristicsOptions() {
      return this.$store.getters['options/getBeachCharacteristics'];
    },
    moreBeachInfoOptions() {
      return this.$store.getters['options/getMoreBeachInfo'];
    },
    beachInformationOptions() {
      return this.$store.getters['options/getBeachInformation'];
    },
    beachPathTypeOptions() {
      return this.$store.getters['options/getBeachPathType'];
    },
    cBeachCharacteristic() {
      return this.property.beachAvail ? this.property.beachCharacteristics : [];
    },
    cDistanceToBeach() {
      return this.property.beachAvail ? this.property.distanceToBeach : null;
    },
    cDistanceToBeachUnit() {
      return this.property.beachAvail ? this.property.distanceToBeachUnit : null;
    },
    cBeachInformation() {
      return this.property.beachAvail ? this.property.beachInformation : [];
    },
    cBeachPathType() {
      return this.property.beachAvail ? this.property.beachPathType : [];
    },
    cMoreBeachInfo() {
      return this.property.beachAvail ? this.property.moreBeachInfo : [];
    },
    cBeachIsPrivate() {
      return this.property.beachAvail ? this.property.beachIsPrivate : null;
    },
    cBeachIsNude() {
      return this.property.beachAvail ? this.property.beachIsNude : null;
    },
    cTrainStationName() {
      return this.property.trainAvailable ? this.property.trainStationName : null;
    },
    cDistanceToTrain() {
      return this.property.trainAvailable ? this.property.distanceToTrain : null;
    },
    cTrainDistanceUnit() {
      return this.property.trainAvailable ? this.property.trainDistanceUnit : null;
    },
  },
  components: {},
  methods: {
    async save() {
      if (this.isLoading) {
        return;
      }
      this.isLoading = true;
    },
  },

};
</script>
