<template>
  <div>
    <b-tab
      :title="tabName"
      :title-link-class="linkClass()"
      @click="activate()">
        <span class="text-right" style="float:right;"  type="button" title="delete this room">
        <b-icon icon="trash" variant="danger" scale="1.5"></b-icon>
        </span>
      <div id="id_Hotel_Bar_Number">
        <span>
          <a class="glyphicon glyphicon-trash"
          style="float:right;" href="#"
          id="jsButtonCloseBarsParent"
          data-toggle="tooltip" title="Delete row">
          </a>
          </span>
          <br>
        <b-row>
          <b-col cols="4">
            <label>Bar name
            <input class="form-pine"
            type="text"
            id="id_Bar_Name">
            </label>
          </b-col>
          <b-col cols="4">
            <label>Bar type
            <select class="form-pine" id="id_Bar_Type">
              <option value="">Select Option</option>
              <option value="Beach Bar">Beach Bar</option>
              <option value="Beer Garden">Beer Garden</option>
              <option value="Cocktail Bar">Cocktail Bar</option>
              <option value="Fireplace Bar">Fireplace Bar</option>
              <option value="Hotel Bar">Hotel Bar</option>
              <option value="Lobby Bar">Lobby Bar</option>
              <option value="Lounge Bar">Lounge Bar</option>
              <option value="Piano Bar">Piano Bar</option>
              <option value="Pool Bar">Pool Bar</option>
              <option value="Pub">Pub</option>
              <option value="Rooftop Bar">Rooftop Bar</option>
              <option value="Snack Bar">Snack Bar</option>
              <option value="Sports Bar">Sports Bar</option>
              <option value="Swim-up Bar">Swim-up Bar</option>
              <option value="Wine Cellar">Wine Cellar</option>
            </select>
            </label>
          </b-col>
          <b-col cols="4">
            <label>Charges
            <select class="form-pine"
            id="id_Bar_Charges">
              <option value="">Select Option</option>
              <option value="0">Inclusive</option>
              <option value="1">Additional Charge</option>
            </select></label>
          </b-col>
        </b-row>
      </div>
  </b-tab>
</div>
</template>
<script>
export default {
  name: 'BarsInstance',
  props: {
    barId: { type: Number },
    isSelected: { type: Boolean, default: false },
  },
  data() {
    return {
      barName: '',
      onGroundFloor: false,
    };
  },
  computed: {
    tabName() {
      return this.barName === '' || this.barName === null ? 'New Bar' : this.barName;
    },
  },
  methods: {
    activate() {
      this.$emit('activated', {
        barId: this.barId,
      });
    },
    linkClass() {
      return this.isSelected ? ['bg-primary', 'text-light'] : ['bg-light', 'text-info'];
    },
  },
};
</script>
