          <option>choice of 3-course meals</option>
          <option>choice of 4-course meals</option>
          <option>choice of 5-course meals</option>
          <option>credit of speciallity restaurant</option>
          <option>choice of meals</option>
          <option>lunch box</option>
          <option>light lunch</option>
          <option>set meal</option>
          <option>set menu or buffet</option>
          <option>snack</option>
          <option>table d'hote</option>
        </select>

        <span>Selected: {{ HotelLunchTypes }}</span>
      </div>
      <div class="form-group">
        <label>Dinner Type<span class="text-danger">*</span></label>
        <select multiple
          class="ananasmultiselect"
          v-model="property.HotelDinnerTypes">
          <option>a la carte</option>
          <option>Buffet</option>
          <option>choice of 2-course meals</option>
          <option>choice of 3-course meals</option>
          <option>choice of 4-course meals</option>
          <option>choice of 5-course meals</option>
          <option>credit of speciallity restaurant</option>
          <option>choice of meals</option>
          <option>lunch box</option>
          <option>light lunch</option>
          <option>set meal</option>
          <option>set menu or buffet</option>
          <option>snack</option>
          <option>table d'hote</option>
        </select>

        <span>Selected: {{ HotelDinnerTypes }}</span>
        </div>
        <b-row>
          <b-col>
        <label class="checkboxcontainer">Dress code required for Dinner
          <input class="form-pine"
          type="checkbox"
          v-model="property.DinnerDressCodeRequired">
          <span class="checkmark"></span></label>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <label>Special corners and requests
            <select multiple
            class="ananasmultiselect"
            v-model="property.HotelFoodSpecials">
            <option value="null" selected>--Select Option--</option>
            <option
              v-for="(a, index) in FoodSpecialsTypes"
              :key="index"
              :value="a">
              {{ a }}
            </option>
            </select>
            </label>
          </b-col>
        </b-row>
        <b-row>
          <b-col cols="3">
        <label class="checkboxcontainer">Christmas Dinner
          <input class="form-pine"
          type="checkbox"
          v-model="property.HotelHasChristmasDinner">
          <span class="checkmark"></span></label>
          </b-col>
        <b-col cols="9" v-if="property.HotelHasChristmasDinner">
          <b-row>
          <b-col>
            <div>
            <label>Charges
              <select class="form-pine"
              v-model="property.HotelChristmasDinnerCharges">
                 <option value="null" selected>--Select Option--</option>
                  <option
                    v-for="(a, index) in chargesTypes"
                    :key="index"
                    :value="a"
                  >
                    {{ a }}
                  </option>
                </select>
            </label>
          </div>
        <div>
          <label class="checkboxcontainer">Obligatory
            <input class="form-pine"
            type="checkbox"
            v-model="property.HotelChristmasDinnerObligatory">
            <span class="checkmark"></span></label>
        </div>
        <div>
          <label class="checkboxcontainer">Drinks Included ?
            <input class="form-pine"
            type="checkbox"
            v-model="property.HotelChristmasDinnerDrinks">
            <span class="checkmark"></span></label>
        </div>
            </b-col>
          </b-row>
          </b-col>
        </b-row>
        <b-row>
          <b-col cols="3">
        <label class="checkboxcontainer">New Year's Eve Dinner
          <input class="form-pine"
          type="checkbox"
          v-model="property.HotelHasNYEDinner">
          <span class="checkmark"></span></label>
          </b-col>
          <b-col cols="9" v-if="property.HotelHasNYEDinner">
            <b-row>
              <b-col>
                <div>
