                  >
                    {{ a }}
                  </option>
                  </select>
                </label>
                </div>
                <div>
                <label class="checkboxcontainer">Obligatory
                  <input class="form-pine"
                  type="checkbox"
                  v-model="property.HotelNYEObligatory">
                  <span class="checkmark"></span></label>
                </div>
              <div>
                <label class="checkboxcontainer">Drinks Included ?
                  <input
                  class="form-pine"
                  type="checkbox"
                  v-model="property.HotelNYEDrinks">
                  <span class="checkmark"></span></label>
              </div>
              </b-col>
            </b-row>
          </b-col>
        </b-row>
        <b-row>
          <b-col cols="3">
        <label class="checkboxcontainer">Other Gala Dinner
          <input class="form-pine"
          type="checkbox"
          v-model="property.HotelHasOtherGala">
          <span class="checkmark"></span></label>
          </b-col>
        <b-col cols="9" v-if="property.HotelHasOtherGala">
          <b-row>
            <b-col>
              <div><label>Specify Gala Details</label></div>
              <div>
              <label>Name
              <input class="form-pine"
              type="text"
              v-model="property.HotelGalaName">
              </label>
              </div>
              <div>
              <label>Date
              <input type="date"
              v-model="property.HotelGalaDate">
              </label>
              </div>
              <div>
              <label>Charges
              <select class="form-pine"
              v-model="property.HotelGalaCharges">
            <option value="null" selected>--Select Option--</option>
            <option
              v-for="(a, index) in chargesTypes"
              :key="index"
              :value="a"
            >
              {{ a }}
            </option>
              </select>
              </label>
              </div>
              <div>
              <label class="checkboxcontainer">Obligatory
                <input class="form-pine"
                type="checkbox"
                v-model="property.HotelGalaObligatory">
                <span class="checkmark"></span>
                </label>
              </div>
              <div>
              <label class="checkboxcontainer">Drinks Included ?
                <input class="form-pine"
                type="checkbox"
                v-model="property.HotelGalaDrinks">
                <span class="checkmark"></span>
                </label>
              </div>
            </b-col>
          </b-row>
        </b-col>
        </b-row>
    <button type="submit" class="btn btn-primary btn-lg">
    SAVE
    <span v-if="isLoading" class="spinner-border"></span>
</button>
</form>
</div>
</template>

<script>

export default {
  name: 'FoodAndBeverage',
  data() {
    return {
      isLoading: false,
      property: this.$store.getters['FoodAndBeverage/getProperty'],
      chargesTypes: ['Inclusive', 'Free of Charge', 'Free for Boards'],
      FoodSpecialsTypes: [
        'afternoon tea included',
        'farmers buffet',
        'side dishes buffet',
        'organic food',
        'bread service for additional charge',
        'dessert buffet',
        'dietary food',
        'farmers buffet on occasion',
        'desert buffet on occasion',
        'gala dinner on occasion',
        'BBQ night on occasion',
        'show cooking on occasion',
        'themed buffet on occasion',
        'gluten free meals',
        'gluten free meals (on request)',
        'BBQ night',
        'afternoon coffee included',
        'afternoon coffee/cake included',
        'free of lactose',
        'free of lactose (on request)',
        'packed lunch',
        'lunch snack',
        'salad buffet with dinner',
        'salad buffet for lunch',
        'show cooking',
        'themed buffet',
        'vegan',
        'vegetarian',
        'healthy section',
        'appetiser buffet',
        'ayurveda',
      ],
    };
  },
  computed: {
    boardsavailable: [],
  },
  methods: {
    convertTimeFormat(inputTime) {
      const date = new Date(inputTime);
      const resultDate = `${date.getUTCFullYear()}-${
        date.getUTCMonth() + 1
      }-${date.getUTCDate()}`;
      const resultTime = `${date.getUTCHours()}:${date.getUTCMinutes()}:${date.getUTCSeconds()}.${date.getUTCMilliseconds()}`;
      return `${resultDate}T${resultTime}Z`;
    },
    async getInfo() {
      if (this.isLoading) {
        return;
      }
      this.isLoading = true;
      await this.$store
        .dispatch('mainInfo/getInfo', 1)
        .then((response) => {
          this.property = this.$store.getters['FoodAndBeverage/getProperty'];
          this.isLoading = false;
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
        });
    },
    async save() {
      if (this.isLoading) {
        return;
      }
      this.isLoading = true;
      const foodAndBeverageToUpdate = {
        boardsavailable: this.property.boardsavailable,
        HotelBreakfastTypes: this.property.HotelBreakfastTypes,
        HotelLunchTypes: this.property.HotelLunchTypes,
        HotelDinnerTypes: this.property.HotelDinnerTypes,
        HotelFoodSpecials: this.property.HotelFoodSpecials,
        DinnerDressCodeRequired: this.property.DinnerDressCodeRequired,
        HotelHasChristmasDinner: this.property.HotelHasChristmasDinner,
        HotelChristmasDinnerCharges: this.property.HotelChristmasDinnerCharges,
        HotelChristmasDinnerObligatory: this.property.HotelChristmasDinnerObligatory,
        HotelChristmasDinnerDrinks: this.property.HotelChristmasDinnerDrinks,
        HotelHasNYEDinner: this.property.HotelHasNYEDinner,
        HotelNYECharges: this.property.HotelNYECharges,
        HotelNYEObligatory: this.property.HotelNYEObligatory,
        HotelNYEDrinks: this.property.HotelNYEDrinks,
        HotelHasOtherGala: this.property.HotelHasOtherGala,
        HotelGalaName: this.HotelGalaName,
        HotelGalaDate: this.HotelGalaDate,
        HotelGalaCharges: this.HotelGalaCharges,
        HotelGalaObligatory: this.property.HotelGalaObligatory,
        HotelGalaDrinks: this.property.HotelGalaDrinks,
      };
      await this.$store
        .dispatch('FoodAndBeverage/postInfo', foodAndBeverageToUpdate)
        .then((response) => {
          this.isLoading = false;
          console.log(response);
        })
        .catch((error) => {
          console.log(error.data);
          this.isLoading = false;
        });
    },
  },
};
</script>
