<template>
  <div class="container">
    <h3>Rooms</h3>
    <b-row>
      <b-col cols="12">
        <p>
          <button
            id="btn-add-room"
            class="pull-right" @click="addRoom()">Add Room</button>
        </p>
      </b-col>
    </b-row>
    <!-- this is container of all rooms  -->
    <b-row>
      <b-col cols="12">
        <b-tabs card>
          <!-- this is a room tab, each room must be in a separate tab
      with the name of the room instead of Tab 1 -->

          <!-- End room data -->
          <div v-for="(r, index) of rooms" :key="index">
            <RoomInstance
              :roomId="r.roomId"
              :isSelected="r.isSelected"
              @activated="changeTab"/>
          </div>
        </b-tabs>
        </b-col>
    </b-row>
  </div>
</template>
<script>
import RoomInstance from './RoomInstance.vue';

export default {
  data() {
    return {
      rooms: [{
        roomId: 0,
        isSelected: true,
      }],
    };
  },
  components: {
    RoomInstance,
  },
  computed: {
    lastId() {
      return this.rooms[this.rooms.length - 1].roomId;
    },
  },
  methods: {
    changeTab(value) {
      this.rooms.forEach((room) => {
        room.isSelected = room.roomId === value.roomId;
      });
    },
    addRoom() {
      const newRoom = {
        roomId: this.lastId + 1,
        isSelected: false,
      };
      this.rooms.push(newRoom);
    },
    async save() {
      if (this.isLoading) {
        return;
      }
      this.isLoading = true;
      const RoomsToUpdate = {
        id: this.property.id,
        roomName: this.property.roomName,
        roomSequence: this.property.roomSequence,
        roomNameType: this.property.roomNameType,
        roomView: this.property.roomView,
        roomOverrideCode: this.property.roomOverrideCode,
        roomOnGroundFloor: this.property.roomOnGroundFloor,
        roomStoreyLevel: this.property.roomStoreyLevel,
        roomTotalUnits: this.property.roomTotalUnits,
        roomAdditionalView: this.property.roomAdditionalView,
        roomFeaturesFancy: this.property.roomFeaturesFancy,
        roomFeaturesAuthentic: this.property.roomFeaturesAuthentic,
        roomFeaturesBig: this.property.roomFeaturesBig,
        roomFeaturesBright: this.property.roomFeaturesBright,
        roomFeaturesColorful: this.property.roomFeaturesColorful,
        roomFeaturesComfortable: this.property.roomFeaturesComfortable,
        roomFeaturesContemporary: this.property.roomFeaturesContemporary,
        roomFeaturesCosy: this.property.roomFeaturesCosy,
        roomFeaturesDetached: this.property.roomFeaturesDetached,
        roomFeaturesElegant: this.property.roomFeaturesElegant,
        roomFeaturesLuxurious: this.property.roomFeaturesLuxurious,
        roomFeaturesMediterranean: this.property.roomFeaturesMediterranean,
        roomFeaturesModern: this.property.roomFeaturesModern,
        roomFeaturesPartlyRenovated: this.property.roomFeaturesPartlyRenovated,
        roomFeaturesPleasant: this.property.roomFeaturesPleasant,
        roomFeaturesPractical: this.property.roomFeaturesPractical,
        roomFeaturesRenovated: this.property.roomFeaturesRenovated,
        roomFeaturesSimple: this.property.roomFeaturesSimple,
        roomFeaturesSmall: this.property.roomFeaturesSmall,
        roomFeaturesSpacious: this.property.roomFeaturesSpacious,
        roomFeaturesStylish: this.property.roomFeaturesStylish,
        roomFeaturesTasteful: this.property.roomFeaturesTasteful,
        roomFeaturesTraditional: this.property.roomFeaturesTraditional,
        roomFeaturesVintage: this.property.roomFeaturesVintage,
        roomFeaturesWelcoming: this.property.roomFeaturesWelcoming,
        roomFeaturesWellTended: this.property.roomFeaturesWellTended,
        roomSizeSQM: this.property.roomSizeSQM,
        roomMinAge: this.property.roomMinAge,
        roomSizeComparison: this.property.roomSizeComparison,
        roomSizeComparisonWith: this.property.roomSizeComparisonWith,
        roomAvailbleSingleUse: this.property.roomAvailbleSingleUse,
        roomOccupancies: this.property.roomOccupancies,
        roomLocations: this.property.roomLocations,
        roomAccessTo: this.property.roomAccessTo,
        roomNumberOfBathrooms: this.property.roomNumberOfBathrooms,
        roomBathroomType: this.property.roomBathroomType,
        roomAdditionalGuestBathroomAvailable: this.property.roomAdditionalGuestBathroomAvailable,
        roomBathroomFeature: this.property.roomBathroomFeature,
        roomFacilityMultimedia: this.property.roomFacilityMultimedia,
        roomFacilityEquipment: this.property.roomFacilityEquipment,
        roomFacilityAmenitie: this.property.roomFacilityAmenitie,
        roomBalconyType: this.property.roomBalconyType,
        roomBalconyFacilities: this.property.roomBalconyFacilities,
        roomAdditionalBedLinen: this.property.roomAdditionalBedLinen,
        roomAdditionalTowels: this.property.roomAdditionalTowels,
        roomAdditionalTeaTowels: this.property.roomAdditionalTeaTowels,
        roomAdditionalCleaningTools: this.property.roomAdditionalCleaningTools,
        roomAdditionalFirstSetup: this.property.roomAdditionalFirstSetup,
        roomHasMiniBar: this.property.roomHasMiniBar,
        roomMiniBarItems: this.property.roomMiniBarItems,
        roomMiniBarCharges: this.property.roomMiniBarCharges,
        roomMiniBarOnRequest: this.property.roomMiniBarOnRequest,
        roomMiniBarArrivalWelcome: this.property.roomMiniBarArrivalWelcome,
        roomMiniBarIsEmpty: this.property.roomMiniBarIsEmpty,
        roomMiniBarRestocking: this.property.roomMiniBarRestocking,
        roomMiniBarRestockingFreq: this.property.roomMiniBarRestockingFreq,
        roomMiniBarRestockingPeriod: this.property.roomMiniBarRestockingPeriod,
        roomMiniBarRestockingCharges: this.property.roomMiniBarRestockingCharges,
        roomMiniBarRestockItems: this.property.roomMiniBarRestockItems,
        roomHasAirCondition: this.property.roomHasAirCondition,
        roomAirConditionCharges: this.property.roomAirConditionCharges,
        roomAirConditionIndividually: this.property.roomAirConditionIndividually,
        roomAirConditionCentralised: this.property.roomAirConditionCentralised,
        roomAirConditionHourly: this.property.roomAirConditionHourly,
        roomAirConditionSeasonal: this.property.roomAirConditionSeasonal,
        roomHasHeating: this.property.roomHasHeating,
        roomHeatingCharges: this.property.roomHeatingCharges,
        roomHeatingFrom: this.property.roomHeatingFrom,
        roomHeatingTo: this.property.roomHeatingTo,
        roomHeatingIsSeasonal: this.property.roomHeatingIsSeasonal,
        roomHasSafeBox: this.property.roomHasSafeBox,
        roomSafeCharges: this.property.roomSafeCharges,
        roomHasLaptopSafe: this.property.roomHasLaptopSafe,
        roomLaptopSafeCharges: this.property.roomLaptopSafeCharges,
        roomHasRoomService: this.property.roomHasRoomService,
        roomRoomServiceCharges: this.property.roomRoomServiceCharges,
        roomRoomServiceAllDay: this.property.roomRoomServiceAllDay,
        roomHasButtlerService: this.property.roomHasButtlerService,
        roomButtlerServiceCharges: this.property.roomButtlerServiceCharges,
        roomHasWifiAccess: this.property.roomHasWifiAccess,
        roomWifiCharges: this.property.roomWifiCharges,
        roomWifiLimitedData: this.property.roomWifiLimitedData,
        roomWifiHighSpeed: this.property.roomWifiHighSpeed,
        roomHasKitchenette: this.property.roomHasKitchenette,
        roomKitchenetteCharges: this.property.roomKitchenetteCharges,
        roomKitchenetteEquipment: this.property.roomKitchenetteEquipment,
        roomKitchenettePrice: this.property.roomKitchenettePrice,
        roomHasKitchenUnit: this.property.roomHasKitchenUnit,
        roomKitchenUnitCharges: this.property.roomKitchenUnitCharges,
        roomKitchenUnitEquipment: this.property.roomKitchenUnitEquipment,
        roomKitchenUnitCleaningPrice: this.property.roomKitchenUnitCleaningPrice,
        roomHasKitchen: this.property.roomHasKitchen,
        roomKitchenCharges: this.property.roomKitchenCharges,
        roomKitchenEquipment: this.property.roomKitchenEquipment,
        roomKitchenCleaningPrice: this.property.roomKitchenCleaningPrice,
        roomRooms: this.property.roomRooms,
        roomRoomsConnectionType: this.property.roomRoomsConnectionType,
        roomRoomsConnectionWith: this.property.roomRoomsConnectionWith,
        roomDoubleBeds: this.property.roomDoubleBeds,
        roomKingBeds: this.property.roomKingBeds,
        roomQueenBeds: this.property.roomQueenBeds,
        roomSingleBeds: this.property.roomSingleBeds,
        roomFrenchBeds: this.property.roomFrenchBeds,
        roomSofaBeds: this.property.roomSofaBeds,
        roomPullOutBeds: this.property.roomPullOutBeds,
        roomBunkBeds: this.property.roomBunkBeds,
        additionalBedsAvailable: this.property.additionalBedsAvailable,
        roomAdditionalBeds: this.property.roomAdditionalBeds,
        roomAdditionalBedsPrice: this.property.roomAdditionalBedsPrice,
        roomCleaningAvailable: this.property.roomCleaningAvailable,
        roomCleaningFrequency: this.property.roomCleaningFrequency,
        roomCleaningIsDaily: this.property.roomCleaningIsDaily,
        roomCleaningCharges: this.property.roomCleaningCharges,
        roomCleaningFinalCleaningApply: this.property.roomCleaningFinalCleaningApply,
        roomCleaningFinalCharges: this.property.roomCleaningFinalCharges,
        roomCleaningFinalPrice: this.property.roomCleaningFinalPrice,
        roomTowelChangeAvailable: this.property.roomTowelChangeAvailable,
        roomTowelChangeTimesPerWeek: this.property.roomTowelChangeTimesPerWeek,
        roomTowelChangeIsDaily: this.property.roomTowelChangeIsDaily,
        roomTowelChangeOnRequest: this.property.roomTowelChangeOnRequest,
        roomTowelChangeCharge: this.property.roomTowelChangeCharge,
        roomSheetChangeAvailable: this.property.roomSheetChangeAvailable,
        roomSheetChangeCharges: this.property.roomSheetChangeCharges,
        roomSheetChangeFrequency: this.property.roomSheetChangeFrequency,
        roomSheetChangeIsDaily: this.property.roomSheetChangeIsDaily,
        roomSheetsNotProvided: this.property.roomSheetsNotProvided,
        roomSheetsForAdditionalCharge: this.property.roomSheetsForAdditionalCharge,
        roomTowelsOnGuest: this.property.roomTowelsOnGuest,
        roomTowelsForAdditionalCharge: this.property.roomTowelsForAdditionalCharge,
      };
      await this.$store
        .dispatch('roomsInfo/postInfo', RoomsToUpdate)
        .then((response) => {
          this.isLoading = false;
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
        });
    },
  },
};
</script>
