<script>
import ChartCard from './Cards/ChartCard.vue';
import StatsCard from './Cards/StatsCard.vue';
import LTable from './Table.vue';

export default {
  components: {
    LTable,
    ChartCard,
    StatsCard,
  },
  data() {
    return {
      pieChart: {
        data: {
