    properties() {
      return this.$store.getters['property/getProperties'];
    },
    isAuthenticated() {
      return this.$store.getters['user/isAuthenticated'];
    },
  },
  methods: {
    async getProperties() {
      await this.$store
        .dispatch('property/getInfo')
        .then((response) => {
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
