<template>
  <div>
    <b-tab :title="tabName" :title-link-class="linkClass()" @click="activate()">
      <span
        class="text-right"
        style="float: right"
        type="button"
        title="delete this room"
      >
        <b-icon icon="trash" variant="danger" scale="1.5"></b-icon>
      </span>
      <b-row id="id_HotelRestaurant_Number">
        <b-col cols="6">
          <b-row>
            <b-col cols="4">
              <label>Restaurant name</label>
            </b-col>
            <b-col>
              <input
                class="form-pine"
                type="text"
                id="id_Restaurant_Name"
                name="name-restaurant-name"
              />
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="4">
              <label>Restaurant type</label>
            </b-col>
            <b-col>
              <select class="form-pine" id="id_Restaurant_Type">
                <option value="">Select Option</option>
                <option value="beach restaurant">beach restaurant</option>
                <option value="bistro">bistro</option>
                <option value="brasserie">brasserie</option>
                <option value="creperie">creperie</option>
                <option value="gourmet restaurant">gourmet restaurant</option>
                <option value="grill restaurant">grill restaurant</option>
                <option value="pizzeria">pizzeria</option>
                <option value="pool restaurant">pool restaurant</option>
                <option value="steak house">steak house</option>
                <option value="seafood restaurant">seafood restaurant</option>
                <option value="speciality">speciality</option>
                <option value="Tapas restaurant">Tapas restaurant</option>
                <option value="taverna">taverna</option>
              </select>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="4">
              <label>Cuisine type</label>
            </b-col>
            <b-col>
              <select class="form-pine" id="id_Restaurant_Cuisinet">
                <option value="">Select Option</option>
                <option value="African">African</option>
                <option value="American">American</option>
                <option value="Arabic">Arabic</option>
                <option value="Asian">Asian</option>
                <option value="British">British</option>
                <option value="Chinese">Chinese</option>
                <option value="German">German</option>
                <option value="French">French</option>
                <option value="Greek">Greek</option>
                <option value="Indian">Indian</option>
                <option value="International">International</option>
                <option value="Italian">Italian</option>
                <option value="Japanese">Japanese</option>
                <option value="Caribbean">Caribbean</option>
                <option value="Cuban">Cuban</option>
                <option value="Mediterranean">Mediterranean</option>
                <option value="Mexican">Mexican</option>
                <option value="National">National</option>
                <option value="Oriental">Oriental</option>
                <option value="Scandinavian">Scandinavian</option>
                <option value="South American">South American</option>
                <option value="Thai">Thai</option>
                <option value="Turkish">Turkish</option>
              </select>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="4">
              <label>Restaurant features </label>
            </b-col>
            <b-col>
              <select
                multiple
                class="ananasmultiselect"
                id="id_Restaurant_Features"
              >
                <option value="air condition">air condition</option>
                <option value="fancy">fancy</option>
                <option value="indoor">indoor</option>
                <option value="non smoker">non smoker</option>
                <option value="non smoker area">non smoker area</option>
                <option value="outdoor area">outdoor area</option>
                <option value="outdoor facilities depending on weather">
                  outdoor facilities depending on weather
                </option>
                <option value="panoramic terrace">panoramic terrace</option>
                <option value="seasonal">seasonal</option>
                <option value="showcooking">showcooking</option>
                <option value="terrace">terrace</option>
                <option value="Arabian night style">Arabian night style</option>
                <option value="Asian style">Asian style</option>
                <option value="Chinese style">Chinese style</option>
                <option value="Japanese style">Japanese style</option>
                <option value="Moroccan style">Moroccan style</option>
                <option value="Turksih style">Turksih style</option>
              </select>
            </b-col>
          </b-row>
        </b-col>
        <b-col cols="6">
          <div class="restaurant-input-item" id="id_Restaurant_Number">
            <!--style="padding:12px; border:1px solid orange; margin-bottom:5px;" -->

            <label class="checkboxcontainer"
              >Buffet
              <input
                class="form-pine"
                type="checkbox"
                id="id_Restaurant_IsBuffet"
                name="buffetrestaurant${x}" />
              <br />
              <span class="checkmark"></span
            ></label>
            <label class="checkboxcontainer"
              >A la Carte
              <input
                class="form-pine"
                type="checkbox"
                id="id_Restaurant_Isalacart"
                name="alacartrestaurant${x}" />
              <br />
              <span class="checkmark"></span
            ></label>
          </div>
          <div>
            <label>Opening days </label><br />
            <select
              multiple
              class="ananasmultiselect"
              id="id_Restaurant_WeekDays"
              name="restaurant-character[]"
            >
              <option value="Mon">Mon</option>
              <option value="Tue">Tue</option>
              <option value="Wed">Wed</option>
              <option value="Thu">Thu</option>
              <option value="Fri">Fri</option>
              <option value="Sat">Sat</option>
              <option value="Sun">Sun</option>
            </select>
          </div>
          <div>
            <span style="margin-right: 35px">
              <label
                >From
                <input class="form-pine" type="time" id="id_Restaurant_From" />
              </label>
            </span>
            <span>
              <label
                >To
                <input class="form-pine" type="time" id="id_Restaurant_To" />
              </label>
            </span>
          </div>
        </b-col>

        <b-col cols="12"> </b-col>
        <b-col cols="12"> </b-col>
        <b-col cols="12"> </b-col>
      </b-row>
    </b-tab>
  </div>
</template>
<script>
export default {
  name: 'RestaurantsInstance',
  props: {
    restaurantId: { type: Number },
    isSelected: { type: Boolean, default: false },
  },
  data() {
    return {
      restaurantName: '',
      onGroundFloor: false,
    };
  },
  computed: {
    tabName() {
      return this.restaurantName === '' || this.restaurantName === null
        ? 'New Restaurant'
        : this.barName;
    },
  },
  methods: {
    activate() {
      this.$emit('activated', {
        restaurantId: this.restaurantId,
      });
    },
    linkClass() {
      return this.isSelected
        ? ['bg-primary', 'text-light']
        : ['bg-light', 'text-info'];
    },
  },
};
</script>
